* {
  box-sizing: border-box;
  font-size: 18px;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 50%;
  margin: auto;
  margin-top: 50px;
}
.App .controll-panel {
  width: 100%;
  display: flex;
  justify-content: space-around;
}
.App .controll-panel .array-taker {
  width: 40%;
}
.App .controll-panel .sortmethod-selecto {
  width: 15%;
}
.App .controll-panel .btn {
  width: 10%;
}
.App .controll-panel .speed-controller {
  width: 35%;
}
.App .controll-panel .speed-controller input {
  width: 100%;
}
.App .controll-panel .speed-controller .speed-ticks {
  display: flex;
  justify-content: space-between;
}
.visualizer {
  margin-top: 70px;
  width: 100%;
  box-shadow: 0 0 5px gray;
  padding: 10px;
}
.visualizer ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.visualizer ul .num-bar {
  background-color: aqua;
  margin: 5px;
  font-size: 20px;
  padding: 5px;
}
@media (max-width: 1000px) {
  .App {
    width: 100%;
  }
}
@media (max-width: 700px) {
  .App .controll-panel {
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  .App .controll-panel .array-taker {
    width: 70%;
  }
  .App .controll-panel .sortmethod-selector {
    width: 30%;
    margin-top: 10px;
  }
  .App .controll-panel .btn {
    width: 30%;
    margin-top: 5px;
  }
  .App .controll-panel .speed-controller {
    width: 50%;
  }
}
